.login {
	width: 100vw;
	height: 100vh;
	background: rgb(24, 119, 242);
	background: linear-gradient(
		170deg,
		rgb(0, 98, 226) 24%,
		rgba(220, 255, 255, 1) 110%
	);
	display: flex;
	align-items: center;
	justify-content: center;
}

.logo-img {
	height: 29px;
	width: 29px;
}

.loginWrapper {
	width: 70%;
	min-width: 800px;
	height: 70%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	max-width: 800px;
}

.loginLeft,
.loginRight {
	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: 10px auto;
}

.loginLeft {
	padding-bottom: 100px;
	margin-right: 100px;
}

.loginLogo {
	font-size: 70px;
	font-weight: 900;
	color: #1775ee;
	margin-bottom: 10px;
	-webkit-text-stroke-width: 1px;
	-webkit-text-stroke-color: rgb(255, 255, 255);

	width: 80%;
	margin: 10px auto 30px;
	letter-spacing: 0.5px;
}

.loginDesc {
	font-size: 20px;
	color: aliceblue;
	font-weight: 400;
	width: 80%;
	margin: 10px auto 30px;
	max-width: 200px;
	letter-spacing: 0.5px;
}

.react-span2 {
	color: #006cf8;
	font-size: 28px;
	font-weight: 600;
	letter-spacing: 0.5px;
	padding-left: 20px;
}

.loginBox {
	height: 350px;
	min-width: 300px;
	padding: 20px;
	background-color: white;
	border-radius: 10px;
	align-items: center;
	display: flex;
	flex-direction: column;
	justify-content: center;
	border: 1px solid rgb(190, 203, 247);
	border-radius: 10px;
	align-items: center;
	box-shadow: rgba(0, 0, 0, 0.35) 0px 1px 10px;
	-webkit-box-shadow: rgba(0, 0, 0, 0.35) 0px 1px 10px;
	-moz-box-shadow: rgba(0, 0, 0, 0.35) 0px 1px 10px;
	background: linear-gradient(
		165deg,
		rgba(24, 119, 242, 0.2) 50%,
		rgba(255, 255, 255, 0.2) 100%
	);
}

.loginInput {
	height: 45px;
	border-radius: 10px;
	border: 1px solid lightgray;
	font-size: 18px;
	padding-left: 20px;
	margin: 10px auto;
	min-width: 250px;
}

.loginInput:focus {
	outline: none;
}

.loginButton {
	height: 45px;
	border-radius: 10px;
	border: none;
	background-color: #1775ee;
	border: 1px solid lightgray;
	color: white;
	font-size: 20px;
	font-weight: 500;
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	padding-right: 30px;
	width: 60%;
	max-width: 200px;
	margin: 25px auto 20px;
}

.loginForgot {
	text-align: center;
	color: #ffffff;
	padding: 10px;
	margin-bottom: 100px;
	cursor: pointer;
}

.loginRegisterButton {
	width: 60%;
	align-self: center;
	max-width: 200px;
	height: 45px;
	border-radius: 10px;
	border: none;
	background-color: #0e992c;
	color: white;
	font-size: 20px;
	font-weight: 500;
	cursor: pointer;
	border: 1px solid lightgray;
}

@media screen and (max-width: 700px) {
	.login {
		height: 900px;
		width: auto;
		overflow-y: scroll;
		overflow-x: hidden;
	}
	.loginWrapper {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		max-width: 280px;
		margin: 100px 1px;
	}

	.loginLeft,
	.loginRight {
		flex: 1;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		max-width: 280px;
	}

	.loginLeft {
		margin: 10px auto;
		padding: 10px;
	}
	.loginRight {
		padding-bottom: 100px;
	}

	.loginBox {
		height: 300px;
		align-items: center;
		display: flex;
		flex-direction: column;
		justify-content: center;
		width: 80%;
		margin-bottom: 200px;
		padding: 10px 0px;
		margin: 10px;
		min-width: 250px;
	}

	.loginInput {
		height: 40px;
		font-size: 15px;
		padding-left: 10px;
		margin: 8px auto;
		min-width: 180px;
	}

	.loginLogo {
		font-size: 50px;
		font-weight: 900;
		color: #1775ee;
		margin-bottom: 10px;
		-webkit-text-stroke-width: 1px;
		-webkit-text-stroke-color: rgb(255, 255, 255);

		width: 100%;
		margin: 10px auto 20px;
		letter-spacing: 1.5px;
	}

	.loginDesc {
		font-size: 22px;
		color: rgb(255, 255, 255);
		text-shadow: 1px 1px rgb(68, 68, 68);
		font-weight: 600;
		width: 85%;
		margin: 5px auto;
		max-width: 200px;
		letter-spacing: 0.5px;
	}

	.loginButton {
		height: 40px;
		padding-right: 15px;
		font-size: 16px;
		max-width: 125px;
		margin: 25px auto 15px;
	}

	.logo-img {
		height: 20px;
		width: 20px;
	}

	.loginRegisterButton {
		max-width: 125px;
		height: 40px;
		font-size: 16px;
	}
}
